/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1.5;
  font-family: 'Noto Sans TC', sans-serif;
  font-family: 'Roboto', sans-serif; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-align: center;
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }

@media (max-width: 991px) {
  .container {
    max-width: 720px; } }

@media (max-width: 768px) {
  .container {
    max-width: 372px; } }

.header {
  background: #AA0601;
  padding-top: 16px;
  padding-bottom: 16px; }

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (max-width: 768px) {
  .nav-bar {
    flex-direction: column;
    align-items: center; } }

.logo a {
  background-image: url(../images/logo-white.png);
  width: 80px;
  height: 50px;
  background-size: cover;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
  display: block; }

.menu {
  display: flex; }

.menu li a {
  color: white;
  display: block;
  font-size: 20px;
  padding: 10px 32px;
  text-align: center; }

.menu li a:hover {
  color: #AA0601;
  background: white; }

@media (max-width: 768px) {
  .logo {
    margin-bottom: 12px; }
  .menu {
    flex-wrap: wrap; }
  .menu li {
    width: 50%;
    border: 1px solid white; } }

.footer {
  background: #AA0601;
  padding-top: 48px;
  padding-bottom: 32px;
  color: #FFFFFF; }

.footer-menu {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #FFFFFF; }

.menu-list a {
  color: #FFFFFF;
  font-size: 16px;
  display: block;
  padding: 5px 24px; }

.ml--24 {
  margin-left: -24px; }

.menu-list {
  display: flex;
  justify-content: space-between; }

.menu-list a:hover {
  background: #FFFFFF;
  color: #AA0601; }

.social {
  display: flex; }

.social a {
  padding: 5px 16px; }

.mr--16 {
  margin-right: -16px; }

.social img {
  width: 48px;
  height: 48px; }

.social img:hover {
  opacity: 0.8; }

.footer-contact {
  padding-top: 24px;
  padding-bottom: 32px; }

.footer-contact a {
  color: #FFFFFF; }

.footer-contact a:hover {
  color: #AA0601;
  background: #FFFFFF; }

.tel {
  font-size: 36px;
  margin-left: 20px; }

.mail {
  font-size: 36px;
  margin-left: 20px; }

.copyright {
  display: flex;
  justify-content: space-between;
  padding-top: 32px; }

.fs-36 {
  font-size: 36px; }

.other-serve {
  display: flex;
  justify-content: space-between;
  font-size: 14px; }

.other-serve a {
  color: #FFFFFF;
  padding: 5px 24px; }

.mr--24 {
  margin-right: -24px; }

.other-serve a:hover {
  color: #AA0601;
  background: #FFFFFF; }

@media (max-width: 991px) {
  .social img {
    width: 40px;
    height: 40px; }
  .social a {
    padding: 5px 8px; } }

@media (max-width: 768px) {
  .menu-contact {
    margin-top: -24px; }
  .menu-list {
    display: none; }
  .tel, .mail {
    font-size: 16px;
    margin-left: 11px; }
  .fs-36 {
    font-size: 18px; }
  .social img {
    width: 24px;
    height: 24px; }
  .copyright {
    flex-direction: column; }
  .copyright p {
    margin-bottom: 8px; }
  .other-serve {
    flex-direction: column; }
  .other-serve a {
    font-size: 16px;
    padding: 0px 0px; } }
